@import url('https://fonts.googleapis.com/css2?family=Open+Sans&display=swap');

* {
  color: #fff;
  font-family: 'Open Sans', sans-serif;
}

body {
  background: #060014;
  background-image: url("https://media.discordapp.net/attachments/806487100886745099/1073322139001831444/bb_grid.png?width=831&height=467");
  background-size: cover;
  background-repeat: no-repeat;
  background-attachment: fixed;
}

.container {
  margin-top: 100px;
}

#output {
  max-height: 70vh;
  overflow: auto;
}

.mask {
  position: absolute;
  bottom: 10%;
  left: 0;
  width: 100%;
  height: 50vh;
  background: linear-gradient(to bottom, rgba(0, 0, 0, 0) 0%, rgb(0, 0, 0) 100%);
  pointer-events: none;
}

.response {
  background: #ffffff17;
  box-shadow: 2px 2px 15px rgb(0, 0, 0);
  border-radius: 20px;
  margin-top: 30px;
  padding: 10px;
  padding-left: 15px;
  margin-bottom: 10px;
  backdrop-filter: blur(3px);
}

.main-out .response:last-child {
  margin-bottom: 90px;
}

.sticky-bottom {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  padding: 10px;
  background: linear-gradient(to top, rgb(0, 0, 0), transparent);
  display: flex;
  align-items: center;
  justify-content: center;
}

.form-group {
  margin-bottom: 0;
}

.btn-block {
  width: 100%;
}

#message-input {
  padding-left: 10px;
  z-index: 100;
  color: #000000;
  min-width: 600px;
  background-color: rgb(255, 255, 255);
  border: none;
  height: 33px;
  border-radius: 10px;
}

input[type='text']:focus {
  outline: none;
}

.btn {
  background: rgb(187, 187, 187);
}

.datetime {
  position: absolute;
  font-size: 16px;
  padding: 24px;
  color: #ffffff;
  background: transparent;
  border-bottom-left-radius: 8px;
  border-bottom-right-radius: 8px;
  width: 250px;
  font-weight: 500;
  font-family: 'Inter', sans-serif;
}

.time {
  text-align: left;
  font-size: 15px;
  color: #ffffff;
}

.date {
  text-align: left;
  margin-top: 7px;
  font-size: 11px;
}

.temp {
  opacity: 0;
}

#weather {
  right: 0;
  position: absolute;
  font-size: 16px;
  padding: 24px;
  color: #ffffff;
  background: transparent;
  border-bottom-left-radius: 8px;
  border-bottom-right-radius: 8px;
  width: 250px;
  font-weight: 500;
  font-family: 'Inter', sans-serif;
}

#weather #temperature {
  text-align: right;
  font-size: 15px;
  color:#ffffff;
}

#weather #description {
  text-align: right;
  margin-top: 7px;
  font-size: 11px;
}

#weather #location {
  text-align: right;
  margin-top: 7px;
  font-size: 11px;
}

.sticky-bottom {
  padding-top: 100px;
}

*::-webkit-scrollbar {
  width: 5px;
}

*::-webkit-scrollbar-track {
  background: #13121200;
  border-radius: 50px;
}

*::-webkit-scrollbar-thumb {
  background: #666;
  border-radius: 50px;
}

@media screen and (max-width: 600px) {
  #message-input {
    min-width: 300px;
  }
}

#typing {
  display: none;
  margin-top: 5px;
}

.dot {
  display: inline-block;
  width: 8px;
  height: 8px;
  margin-right: 4px;
  border-radius: 50%;
  background-color: #ddd;
  animation: typing 1s infinite;
}

@keyframes typing {
  0%,
  20%,
  80%,
  100% {
    transform: scale(1);
  }
  40% {
    transform: scale(1.3);
  }
  60% {
    transform: scale(1.2);
  }
}

